/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import MuiButton from '@mui/material/Button'
import EditIcon from '@mui/icons-material/Edit'
import StopIcon from '@mui/icons-material/Stop'
import log from 'loglevel'

import { User, Sample, UserPermissions } from '../../types'
import { SampleInfoPanel } from '../SampleInfoPanel'
import { NAMCResource } from '@namcbugdb/common'

export interface SampleSortingProps {
  sorterUsers: User[]
  sample: Sample
  permissions: UserPermissions
}

export const SampleSorting = ({ sorterUsers, sample, permissions }: SampleSortingProps): JSX.Element => {
  // STATE

  const [isEditing, setIsEditing] = useState(false)

  // EFFECTS

  useEffect(() => {
    // actions.reset(sample)
    log.debug('load sample', sample)
  }, [sample])

  // RENDER

  return (
    <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      <SampleInfoPanel
        sample={sample}
        actions={
          permissions.updateAny?.includes(NAMCResource.samples) && (
            <MuiButton
              onClick={() => {
                setIsEditing(!isEditing)
              }}
              startIcon={isEditing ? <StopIcon /> : <EditIcon />}
              variant={isEditing ? 'contained' : 'outlined'}
            >
              {isEditing ? 'Stop Sorting' : 'Start Sorting'}
            </MuiButton>
          )
        }
      />
      <div>sort sample</div>
    </div>
  )
}
