import React, { Suspense } from 'react'
import { useParams } from 'react-router'
import { useRecoilValue } from 'recoil'

import { selectSample, selectSorterUsers, selectMyProfile } from '../../data'
import { SampleSorting as Component } from './SampleSorting'
import { Fallback } from './Fallback'

const Connector = (): JSX.Element => {
  const { sampleId } = useParams()

  const sorterUsers = useRecoilValue(selectSorterUsers)
  const permissions = useRecoilValue(selectMyProfile).permissions
  const sample = useRecoilValue(selectSample({ id: Number(sampleId) }))

  if (sample === null) return <div>Sample not found.</div>

  return <Component sorterUsers={sorterUsers} sample={sample} permissions={permissions} />
}

export const SampleSorting = (): JSX.Element => (
  <Suspense fallback={<Fallback />}>
    <Connector />
  </Suspense>
)
